header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #8a2b06;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 3;
}

.image {
    width: 100%;
    height: 25rem;
    color: #fff;
    z-index: 0;
    overflow: hidden;
}
  
.image img {
    width: 110%;
    height: 100%;
    object-fit: cover;
    transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}
  
@media screen and (max-width: 600px) {
    .h1--hide {
      display: none;
    }
    header {
      justify-content: right;
    }
}