.button {
  cursor: pointer;
  font: inherit;
  border: none;
  background-color: #4d1601;
  color: white;
  padding: 0.75rem 3rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 25px;
  font-weight: bold;
}

.button:hover {
  background-color: #2c0d00;
}

.icon-cart {
  width: 1.35rem;
  height: 1.35rem;
  margin-right: 0.5rem;
}

.icon-white {
  color: #fff;
}

.number-item {
  background-color: #b94517;
  padding: 0.25rem 1rem;
  border-radius: 25px;
  margin-left: 1rem;
  font-weight: bold;
}

.button:hover .number-item {
  background-color: #92320c;
}

@media screen and (max-width: 600px) {
  .text-cart {
    display: none;
  }
}